import React, { Component } from 'react'
import { connect } from 'react-redux'
import { createProject } from '../../store/actions/projectActions'
import { Redirect } from 'react-router-dom';


class CreateProject extends Component {
  state = {
    title: '',
    content: ''
  }
  handleChange = (e) => {
    this.setState({
      [e.target.id]: e.target.value
    })
  }
  handleSubmit = (e) => {
    e.preventDefault();
    // console.log(this.state);
    this.props.createProject(this.state);
    this.props.history.push('/');
    this.setState({
      title: '',
      content: ''
    });
  }
  render() {
    const { auth } = this.props
    if(!auth.uid){
      return <Redirect to="/signin"/>
    }
    return (
      <div className="container CreateProject">
        <form className="white" onSubmit={this.handleSubmit}>
          <h5 className="grey-text text-darken-3">Create a New Project</h5>
          <div className="input-field">
            <input type="text" id='title' onChange={this.handleChange} value={this.state.title} />
            <label htmlFor="title">Project Title</label>
          </div>
          <div className="input-field">
            <textarea id="content" className="materialize-textarea" onChange={this.handleChange} value={this.state.content}></textarea>
            <label htmlFor="content">Project Content</label>
          </div>
          <div className="input-field">
            <button type="submit" className="btn pink lighten-1" disabled={!(this.state.title && this.state.content) >0}>Create</button>
          </div>
        </form>
      </div>
    )
  }
}

const mapStateToProps= (state)=>{
  return{
    auth:state.firebase.auth
  }
}
const mapDispatchToProps = dispatch => {
  return {
    createProject: (project) => dispatch(createProject(project))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(CreateProject)
